body{
  overflow:hidden;
}

a{
  text-decoration: none;

}

.App {
  text-align: center;
}

.Page{
    height: 100vh;
    width: 100vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack:center;
        justify-content:center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.NavButton{
    color: white;
    background-color: black;
    border-radius: 3px;
    padding: 0.7em;
    margin: 1em;
    width: 5em;
    height: 1em;
}

.PageContent{
    width: 100%;
    padding-top: 2em;
}

@-webkit-keyframes slide{
    from {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    to{
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
}

@keyframes slide{
    from {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    to{
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
}


